<template>
  <el-tabs v-model="activeName" @tab-click="handleClick">
    <el-tab-pane label="用户画像" name="first">
      <div class="p_6">
        <div class="search-wrapper">
          <div class="search-content">
            <el-select v-model="searchParam.personaType" slot="prepend" placeholder="请选择" style="width: 150px;">
              <el-option
                v-for="item in personasTypes"
                :key="item.value"
                :label="item.name"
                :value="item.value">
              </el-option>
            </el-select>
            <el-select v-model="searchParam.device" slot="prepend" placeholder="手机设备" style="width: 100px;">
              <el-option
                v-for="item in deviceOptions"
                :key="item.value"
                :label="item.name"
                :value="item.value">
              </el-option>
            </el-select>
            <!--<el-date-picker-->
              <!--v-model="searchParam.dateRange"-->
              <!--type="daterange"-->
              <!--placeholder="选择日期范围">-->
            <!--</el-date-picker>-->
            <el-date-picker
              v-model="searchParam.date"
              type="date"
              placeholder="选择日期范围">
            </el-date-picker>
          </div>
          <div class="search-btn">
            <el-button slot="append" icon="search" @click="search" style="width:80px; ">查询</el-button>
          </div>
        </div>
        <el-table
          :data="tableData" style="width: 100%" border :default-sort="{prop: 'date', order: 'descending'}">
          <el-table-column
            type="index"
            align="center"
            width="50">
          </el-table-column>
          <el-table-column prop="personaType" label="特征" align="center" sortable></el-table-column>
          <el-table-column prop="features" label="特征分布" align="center" sortable></el-table-column>
          <el-table-column prop="totalCount" label="总客户数" align="center" width="80" sortable></el-table-column>
          <el-table-column prop="customerCount" label="客户数" align="center" width="80" sortable></el-table-column>
          <el-table-column prop="customerRate" label="客户占比" align="center" sortable></el-table-column>
          <el-table-column prop="passTotalCount" label="审核通过数" align="center" width="80" sortable></el-table-column>
          <el-table-column prop="passTotalRate" label="审核通过占比" align="center" width="80" sortable></el-table-column>

        </el-table>
        <div class="pagination_position">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="pageSizes"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
          </el-pagination>
        </div>
      </div>
    </el-tab-pane>
    <div v-if="showDetail">
      <el-tab-pane name="second">

      </el-tab-pane>
    </div>
  </el-tabs>
</template>

<style scoped>
  .el-select .el-input {
    width: 100px;
  }

  .pagination_position {
    padding-top: 20px;
  }

  .echarts {
    height: 300px;
  }
</style>
<style>
  .el-table .cell, .el-table th > div {
    padding-left: 5px;
    padding-right: 5px;
    box-sizing: border-box;
    text-overflow: ellipsis;
  }
</style>
<script>
  import Common from '../../../utils/common'
  import {Notification} from 'element-ui';
  import formatDate from '../../../utils/formatDate.js'
  export default{
//    components:{
//    },
    data(){
      let date = new Date();
      let prevMonth = new Date(date.setDate(date.getDate() - 7));
      let now = new Date();

      //echats data

      function randomData() {
        return Math.round(Math.random() * 1000);
      }

      return {
        personasTypes: [{
          value: 'all',
          name: '全部'
        }, {
          value: 'gender',
          name: '性别'
        }, {
          value: 'plist_number',
          name: 'plist_number'
        }, {
          value: 'phone_in_use_time',
          name: 'phone_in_use_time'
        }, {
          value: 'id_number_lend_request_info',
          name: 'id_number_lend_request_info'
        }, {
          value: 'device_lend_request_info',
          name: 'device_lend_request_info'
        }, {
          value: 'result_by_miguan',
          name: 'result_by_miguan'
        }, {
          value: 'qianhai_risk_score',
          name: 'qianhai_risk_score'
        }, {
          value: 'rule_id_by_tongdun',
          name: 'rule_id_by_tongdun'
        }, {
          value: 'gps_nearby_1km_lend_request_info',
          name: 'gps_nearby_1km_lend_request_info'
        }, {
          value: 'same_company_info',
          name: 'same_company_info'
        }, {
          value: 'contact_num',
          name: 'contact_num'
        }, {
          value: 'emergency_contact_call_number_rank_list',
          name: 'emergency_contact_call_number_rank_list'
        }, {
          value: 'check_register_phone_in_contact_book',
          name: 'check_register_phone_in_contact_book'
        }, {
          value: 'check_reserved_phone_in_contact_book',
          name: 'check_reserved_phone_in_contact_book'
        }, {
          value: 'province',
          name: '省份'
        }, {
          value: 'profession',
          name: '行业类别'
        },
        ],
        deviceOptions: [{
          value: 'ALL',
          name: 'ALL',
        }, {
          value: 'iPhone',
          name: 'iOS',
        }, {
          value: 'Android',
          name: 'Android'
        }],
        searchParam: {
          personaType: "all",
          device: "ALL",
          dateRange: [prevMonth, now],
          date:now
        },
        activeName: 'first',
        showDetail: false,
        name: 'detail',
        total: 0,
        currentPage: 1,
        pageSizes: [10, 20, 50, 100, 200],
        pageSize: 50,
        tableData: [],
      }
    },
    methods: {
      getPersonasTypes(){
        Common.ajax({
          //url: "/api/loan/personasTypes",
          url: 'api/requestParam/persona',
          type: 'GET',
          contentType: "application/json",
          data: {},
          success: (response) => {
            //console.log(response);
            if (response.data && response.data.list) {
              this.personasTypes = response.data.list;
            }
          },
          error(err){
//            let error = err.message || err;
//            Notification.error({
//              title: '错误',
//              message: error
//            });
          }
        })
      },
      getDevices(){
        Common.ajax({
          url: '/api/requestParam/device',
          type: 'GET',
          contentType: 'application/json',
          success: (response) => {
            if (response.data && response.data.list) {
              this.deviceOptions = response.data.list;
            }
          },
          error: (err) => {
            this.deviceOptions = [{
              value: 'ALL',
              name: 'ALL',
            }, {
              value: 'iPhone',
              name: 'IOS',
            }, {
              value: 'Android',
              name: 'Android'
            }];
          }
        });
      },
      search(){
        let self = this;
        let date = self.searchParam.date;
        //console.log('date',self.searchParam.date);
        Common.ajax({
          url: "/api/loan/persona",
          type: 'POST',
          contentType: "application/json",
          data: {
            pageIndex: self.currentPage,
            pageSize: self.pageSize,
            //device: self.searchParam.device,
            personaType: self.searchParam.personaType,
            date: formatDate(date, 'yyyy-MM-dd'),
            //endTime: formatDate(dateRange[1], 'yyyy-MM-dd')
          },
          success(response){
            //console.log(response);
            if (response.data && response.data.list) {
              let data = response.data;
              self.tableData = data.list;
              self.total = data.pageUtil.pageCount;
            }
          },
          error(err){
            let error = err.message || err;
            Notification.error({
              title: '错误',
              message: error
            });
          }
        })
      },
      removeLabel(event){
        //console.log(event);
        event.stopPropagation();
        this.activeName = 'first';
        this.showDetail = false;

      },
      handleSizeChange(val) {
        this.pageSize = val;
        //console.log(`每页 ${val} 条`);
        this.search();
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        //console.log(`当前页: ${val}`);
        this.search();
      },
      handleClick(tab, event) {
        let index = tab.index;
        if (index == 0) {
          this.activeName = 'first';
        }
      }
    },
    created(){
      //this.search();
      //console.log('created', this);
    },
    mounted(){
      //
      //console.log('mounted', this);
      this.getPersonasTypes();
      this.getDevices();
      this.search();
    }
  }
</script>
